	module.exports = {"data":{"wordpressPost":{"title":"Fitness Guru Massy Arias And Motorcyclist Shelina Moreda Are COVERGIRLS","content":"<p>Massy Arias, fitness trainer, lifestyle guru, and motivational force and Shelina Moreda, professional motorcycle racer and the first female to race at the Indianapolis Motor Speedway, are newly minted COVERGIRLS. And we couldn&#8217;t be more chuffed.</p>\n<p>The two women are undeniable powerhouses and we love seeing beauty brands embracing women from all different areas of life, rather than relying on traditional models.</p>\n<p>After all, it&#8217;s not as though the duo isn&#8217;t beautiful. Both are stunning and also, beauty focused.</p>\n<p>“As anyone who follows me on Instagram knows, I love beauty as much as I love fitness,” says Massy Arias.</p>\n<p>&nbsp;</p>\n<img class=\"alignnone size-medium wp-image-3374\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/07194541/CG-SOCIAL-_-BROAD-OUTREACH-M_B_L_02_MOTIONSET_06_MASSY_0057-300x200.jpg\" alt=\"\" width=\"300\" height=\"200\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/07194541/CG-SOCIAL-_-BROAD-OUTREACH-M_B_L_02_MOTIONSET_06_MASSY_0057-300x200.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/07194541/CG-SOCIAL-_-BROAD-OUTREACH-M_B_L_02_MOTIONSET_06_MASSY_0057-768x512.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/07194541/CG-SOCIAL-_-BROAD-OUTREACH-M_B_L_02_MOTIONSET_06_MASSY_0057-1024x683.jpg 1024w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p><strong>Massy Arias &#8211; trainer, fitness guru and inspiration.</strong></p>\n<p>“There’s a stigma around wearing makeup at the gym, but there are times I proudly rock a full face, especially when I’m filming and want that extra dose of confidence before putting myself out there in front of millions of people.</p>\n<p>&#8220;It’s really a personal choice, and I wish there wasn’t so much judgement around it. If the flick of a cat-eye, or a brick red lip, gives you some extra motivation and feeling of, ‘I got this,’ why not go for it? COVERGIRL’s belief that you can wear makeup when, where and however you want is really about personal transformation – just like fitness.”</p>\n<p>&nbsp;</p>\n<img class=\"alignnone size-medium wp-image-3376\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/07200654/JPG-Massy.Arias--300x300.jpg\" alt=\"\" width=\"300\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/07200654/JPG-Massy.Arias--300x300.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/07200654/JPG-Massy.Arias--150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/07200654/JPG-Massy.Arias-.jpg 590w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p><strong>One impressive woman. Image: Instagram/@massy.arias</strong></p>\n<p>&nbsp;</p>\n<p>Love that. And her!</p>\n<p>As for Shelina Moreda, she says, “Pro racing is a challenging profession – physically, mentally, and emotionally. I love what I do, and people are often surprised because the sport of racing is largely dominated by men. Makeup is something I’ve always enjoyed, and it&#8217;s something that sets me apart when I&#8217;m on the racetrack and the only thing you can see are my eyes peering out of the helmet, so that&#8217;s the part I especially love to play up.&#8221;</p>\n<p>&nbsp;</p>\n<img class=\"alignnone size-medium wp-image-3377\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/07200757/JPG-shelina93racing-300x220.jpg\" alt=\"\" width=\"300\" height=\"220\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/07200757/JPG-shelina93racing-300x220.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/07200757/JPG-shelina93racing.jpg 600w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p><strong>Shelina took to social media to announce her COVERGIRL news. Image: Instagram/@shelina93racing</strong></p>\n<p>&nbsp;</p>\n<p>&#8220;My partnership with COVERGIRL shows the world that it&#8217;s okay to have an active, yet powerful career and still value feeling pretty and I am happy to be able to encourage other people to unapologetically be themselves, pursue what they love, and share it with the world.”</p>\n<p>Massy and Shelina have both overcome personal and professional challenges in order to rise to the top of their games. They&#8217;re now dedicated to helping others do the same.</p>\n<p>Massy channels her passion for wellness towards influencing a new generation of trainers to teach others about a healthy lifestyle, with a focus on increasing their physical abilities and creating longevity and bliss, not just a quick bikini body fix. Shelina runs an all-female motorcycle skills school that focuses on building confidence in girls to push past barriers and reach their individual goals.</p>\n<p>Like it? You bet we do. Nice work COVERGIRL. More, more, more.</p>\n<p>&nbsp;</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/07200757/JPG-shelina93racing.jpg"},"categories":[{"slug":"beauty-talk","name":"Beauty Talk"},{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"30722fbc-f8aa-5db3-a2d4-d88aba6c9667"}}

